export default {
	data() {
		return {
			loading: false,
			dataList: []
		};
	},
	created() {
		this.getData();
	},
	methods: {
		getData() {
			uni.request({
				method: 'GET',
				url: 'https://ncov.zhouxuanyu.com/RumorList',
				success: res => {
					setTimeout(() => {
						this.loading = true;
					}, 500);
					res.data.map(item => {
						item.isShow = false;
						return item;
					});
					this.dataList = res.data;
					console.log(this.dataList);
				}
			});
		},
		cardContentShow(index) {
			this.dataList[index].isShow = !this.dataList[index].isShow;
		}
	}
};
